.partners {
	padding: 43px 0 54px;
	background: $gray_light;
	@include mq($max-width: 1500px) {
		padding: 30px 0; }
	.container {
		max-width: 1560px;
		@include mq($max-width: 1800px) {
			max-width: 1270px; }
		@include mq($max-width: 1500px) {
			max-width: 1100px; } }
	.slider {
		position: relative; }
	.slide {
		margin: 0 15px;
		@include mq($max-width: 575px) {
			margin: 0 7px; }
		&__img {
			width: 100%;
			display: block;
			border-radius: 9px; } } }
