.header {
	padding: 53px 0 0 0;
	.container {
		max-width: 1560px;
		@include mq($max-width: 1650px) {
			padding: 0 15px; } }
	.logo {
		@include mq($max-width: 575px) {
			margin: 0 auto 15px; }
		&__img {
			display: block;
			width: 306px;
			@include mq($max-width: 1500px) {
				width: 250px; }
			@include mq($max-width: 1200px) {
				width: 150px; } } }
	.search {
		width: 307px;
		position: relative;
		@include mq($max-width: 1500px) {
			width: 250px; }
		&__input {
			display: block;
			width: 100%;
			@include trs;
			background: white;
			height: 37px;
			border-radius: 37px;
			padding: 0 45px;
			text-align: right;
			letter-spacing: 0.03em;
			font-size: 14px;
			color: $gray2;
			font-weight: 700;
			&::placeholder {
				font-weight: 700;
				font-size: 14px;
				color: $gray2;
				letter-spacing: 0.03em;
				text-align: right;
				@include trs; }
			&:focus {
				text-align: left;
				&::placeholder {
					text-align: left; } } }
		&__btn {
			position: absolute;
			right: 0;
			top: 0;
			background: none;
			width: 37px;
			height: 37px;
			cursor: pointer;
			border-radius: 37px;
			@include flex(row, center, center, nowrap);
			&:hover {
				background: $green;
				svg {
					circle,
					path {
						stroke: white; } } }
			svg {
				display: block;
				width: 21px;
				circle,
				path {
					@include trs;
					stroke: $green; } } } }
	&-top {
		background: $gray2;
		padding: 8px 0;
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		z-index: 10;
		@include trs;
		&.scroll {
			background: $green;
			.main-menu-btn-icon, .main-menu-btn-icon:before, .main-menu-btn-icon:after {
				background: white; }
			#main-menu>li>a {
				color: white;
				.sub-arrow {
					border-color: white transparent transparent transparent; } } }
		.container {
			@include flex(row, between, center, wrap); } }
	&-bottom {
		padding: 15px 0;
		.container {
			@include flex(row, between, center, wrap); } }
	.links {
		@include flex(row, null, center, wrap);
		margin: 0 0 0 auto;
		@include mq($max-width: 700px) {
			width: 100%; }
		@include mq($max-width: 575px) {
			flex-direction: column;
			align-items: center; }
		&-block {
			@include flex(row, null, center, wrap);
			@include mq($max-width: 575px) {
				margin: 0 0 15px 0; } } }
	.tel {
		margin: 0 65px 0 50px;
		color: $dark;
		font-size: 22px;
		font-weight: 700;
		position: relative;
		@include mq($max-width: 1200px) {
			margin: 0 10px;
			font-size: 16px; }
		@include mq($max-width: 575px) {
			margin: 0; }
		&:hover {
			color: $green; }
		&::before {
			content: '';
			display: inline-block;
			position: absolute;
			left: -50px;
			top: 50%;
			transform: translate(0, -50%);
			width: 38px;
			height: 38px;
			@include bg(null, $green, 'icons/tel.svg', no-repeat, center, center, auto);
			border-radius: 100%;
			@include mq($max-width: 1200px) {
				display: none; } }
		span {
			color: $green; } }
	.callback {
		color: $gray;
		font-weight: 600;
		margin: 0 14px;
		&:hover {
			color: $red; } }
	.choose-link {
		margin: 0;
		background: none;
		cursor: pointer;
		@include mq($max-width: 575px) {
			margin: 0 10px 0 0; }
		svg {
			width: 25px;
			path {
				@include trs;
				fill: transparent;
				stroke: $dark;
				stroke-width: 55; } }
		&:hover {
			svg {
				path {
					stroke: $red;
					fill: $red; } } } }
	.user-link {
		@include flex(row, null, center, wrap);
		margin: 0;
		color: $dark;
		letter-spacing: 0.03em;
		font-weight: 700;
		font-size: 16px;
		margin: 0 27px;
		@include mq($max-width: 1200px) {
			margin: 0 15px; }
		@include mq($max-width: 575px) {
			margin: 0; }
		&:hover {
			color: $green;
			svg {
				circle,
				path {
					stroke: $red; } } }
		svg {
			circle,
			path {
				@include trs;
				stroke: $dark; } }
		span {
			margin: 0 0 0 8px; } }
	.basket-link {
		@include flex(row, null, center, nowrap);
		&:hover {
			svg {
				path {
					stroke: $red; }
				circle {
					fill: $red; } } }
		&-icon {
			position: relative;
			margin: 0 15px 0 0; }
		svg {
			display: block;
			height: 33px;
			path {
				@include trs;
				stroke: $dark; }
			circle {
				@include trs;
				fill: $dark; } }
		.count {
			min-width: 17px;
			height: 17px;
			@include flex(row, center, center, nowrap);
			position: absolute;
			right: 0;
			bottom: 0;
			z-index: 2;
			border-radius: 100%;
			background: white;
			box-shadow: 0 0 0 2px white;
			color: $green;
			font-weight: 700;
			font-size: 13px;
			line-height: 1;
			padding: 0 3px;
			border: 2px solid $green; }
		span {
			font-size: 14px;
			font-weight: 500;
			color: $gray;
			display: inline-block;
			width: 100%;
			max-width: 50px;
			&::after {
				content: '₽';
				color: $gray;
				margin: 0 0 0 5px; } } }

	#main-menu-state:not(:checked) ~ #main-menu {
		@include mq($max-width: 1200px) {
			display: none; } }
	#main-menu-state:checked ~ #main-menu {
		// +mq($max-width: 1200px)
 }		/// +flex(row, null, center, wrap)
	#main-menu {
		@include mq($min-width: 320px) {
			padding: 0;
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			border-radius: 0; }
		@include mq($min-width: 1200px) {
			@include flex(row, null, center, wrap);
			margin: 0;
			padding: 0;
			border-radius: 0;
			border: none;
			background: none;
			box-shadow: none;
			width: auto;
			position: static; }
		ul {
			@include mq($min-width: 320px) {
				margin: 0;
				padding: 0;
				border-radius: 0 !important;
				border: none;
				background: none;
				box-shadow: none;
				line-height: 1;
				opacity: 1;
				&:before,
				&:after {
					display: none; } } }
		li {
			@include mq($min-width: 320px) {
				border-radius: 0;
				border: none !important; }
			@include mq($min-width: 1200px) {
				margin: 0;
				border-radius: 0;
				padding: 0;
				border: none;
				background: none;
				box-shadow: none;
				line-height: 1;
				opacity: 1;
				&:before,
				&:after {
					display: none; } } }
		a {
			@include mq($min-width: 320px) {
				border-radius: 0;
				border: none !important; }
			@include mq($min-width: 1200px) {
				background: none;
				margin: 0;
				border-radius: 0;
				padding: 0;
				border: none;
				box-shadow: none;
				line-height: 1;
				opacity: 1;
				color: black; }
			&.has-submenu {
				position: relative;
				padding-left: 0;
				@include mq($max-width: 1200px) {
					padding-left: 15px; }
				&[aria-expanded="true"] {
					.sub-arrow {
						&:before {
							content: '-'; } } } }
			&.highlighted {
				color: inherit !important;
				background: inherit !important; } }
		.sub-arrow {
			@include mq($max-width: 1200px) {
				width: 30px;
				height: 30px;
				top: 50%;
				right: 10px;
				transform: translate(0, -50%);
				border: 1px solid $dark;
				position: absolute;
				margin: 0;
				z-index: 2;
				&:before {
					content: '+';
					display: block;
					font-size: 15px;
					line-height: 30px; } }
			@include mq($min-width: 1200px) {
				right: 2px; } }
		ul {
			min-width: 100% !important;
			@include mq($min-width: 320px) {
				position: static;
				background: $green; }
			@include mq($min-width: 1200px) {
				position: absolute;
				background: white;
				padding: 35px;
				border: 2px solid $gray2;
				top: 35px !important; }
			ul {
				@include mq($min-width: 320px) {
					position: static;
					margin: 0 !important;
					padding: 0 5px;
					background: white; }
				@include mq($min-width: 1200px) {
					padding: 0 0 0 0;
					position: absolute;
					margin-top: -100% !important;
					margin-left: 35px !important;
					top: -2px !important;
					left: 100% !important; }
				& > li {
					@include mq($min-width: 320px) {
						margin: 0 !important;
						display: inline-block;
						width: 49%; }
					@include mq($min-width: 575px) {
						width: 32%; }
					@include mq($min-width: 700px) {
						width: 19%; }
					@include mq($min-width: 1200px) {
						width: 100%; }
					& > a {
						@include mq($min-width: 320px) {
							color: $dark;
							padding: 10px; } } } }
			& > li {
				width: 100%;
				&:not(:last-child) {
					margin: 0 0 17px 0;
					@include mq($max-width: 1200px) {
						margin: 0 0 5px 0; } }
				& > a {
					font-size: 14px;
					font-weight: 500;
					@include mq($max-width: 1200px) {
						width: 100%;
						color: white;
						padding: 10px 45px 10px 15px; }
					&.has-submenu {
						@include mq($max-width: 1200px) {
							color: white !important;
							background: inherit !important; }
						.sub-arrow {
							right: -12px;
							@include mq($max-width: 1200px) {
								right: 10px;
								border-color: white; } } }
					&.highlighted {
						@include mq($max-width: 1200px) {
							color: white !important; }
						.sub-arrow {
							@include mq($max-width: 1200px) {
								border-color: white;
								color: white; } } }
					@include mq($min-width: 1200px) {
						&:hover {
							color: $pink;
							.sub-arrow {
								border-color: transparent transparent transparent $pink; } } } } } }
		& > li {
			position: relative;
			@include mq($max-width: 1200px) {
				width: 100%; }
			& > a {
				position: relative;
				padding: 0 25px;
				font-size: 14px;
				font-weight: 500;
				color: $dark;
				@include mq($max-width: 1500px) {
					padding: 0 15px; }
				@include mq($max-width: 1200px) {
					padding: 15px 45px 15px 15px; }
				@include mq($min-width: 1200px) {
					&:hover {
						color: $pink; } } } } } }

.sm-clean ul a.current {
	@include mq($min-width: 320px) {
		color: white; } }
