.categories {
	padding: 50px 0;
	@include mq($max-width: 1500px) {
		padding: 30px 0; }
	.container {
		max-width: 1270px;
		@include flex(row, center, end, wrap); }
	.item {
		margin: 0 11px;
		@include mq($max-width: 1000px) {
			margin: 2%; }
		&:hover {
			.item__title {
				color: $pink; }
			.item-icon__static {
				opacity: 0; }
			.item-icon__hover {
				opacity: 1; } }
		&__title {
			text-align: center;
			font-weight: 700;
			font-size: 16px;
			color: $dark;
			margin: 23px 0 0 0; }
		&-icon {
			height: 140px;
			position: relative;
			@include mq($max-width: 1500px) {
				height: 100px; }
			@include mq($max-width: 1200px) {
				height: 75px; }
			&__img {
				display: block;
				height: 100%;
				width: auto;
				@include trs; }
			&__static {}
			&__hover {
				position: absolute;
				bottom: 0;
				left: 50%;
				transform: translate(-50%, 0);
				opacity: 0; } } } }

