.mainblock {
	.container {
		max-width: 1270px;
		@include flex(row, null, null, wrap);
		@include mq($max-width: 1500px) {
			max-width: 1100px; } }
	.slider {
		position: relative; }
	.slide {
		overflow: hidden;
		&-left {
			position: relative;
			z-index: 2;
			padding: 0 70px 0 0;
			max-width: 470px;
			width: 100%;
			min-height: 517px;
			@include flex(column, center, start, null);
			@include mq($max-width: 1500px) {
				min-height: 400px; }
			@include mq($max-width: 700px) {
				max-width: 80%;
				padding: 0 25px 0 0; }
			@include mq($max-width: 575px) {
				max-width: 70%; }
			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				bottom: 0;
				right: 0;
				width: 100vw;
				z-index: -1;
				background: $gray_light; } }
		&-right {
			position: relative;
			width: calc( 100% - 470px );
			@include mq($max-width: 700px) {
				width: 20%; }
			&:before {
				content: '';
				display: block;
				position: absolute;
				background: $slide-gradient;
				left: 0;
				top: 0;
				bottom: 0;
				width: 100vw; } }
		&__title {
			font-size: 45px;
			font-weight: 700;
			line-height: 1;
			margin: 0 0 10px 0;
			@include mq($max-width: 1200px) {
				font-size: 30px; } }
		&__text {
			font-weight: 500;
			font-size: 18px;
			@include mq($max-width: 1200px) {
				font-size: 14px; } }
		&__subtitle {
			margin: 25px 0 14px 0;
			color: $red;
			font-weight: 700;
			font-size: 29px;
			@include mq($max-width: 1200px) {
				font-size: 20px;
				margin: 10px 0; } }
		.pink-btn {
			margin: 30px 0 0 0; } }
	.slide1 {
		.circle {
			@include flex(row, center, center, nowrap);
			background: linear-gradient(222.2deg, #F13535 12.1%, #B61B1B 85.23%);
			box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);
			width: 118px;
			height: 118px;
			border-radius: 100%;
			z-index: 2;
			position: absolute;
			left: 0;
			top: 0;
			transform: translate(50%, 20%) rotate(-18deg);
			@include mq($max-width: 1500px) {
				transform: translate(20%, 20%) rotate(-18deg); }
			@include mq($max-width: 700px) {
				z-index: 4;
				right: 15px;
				transform: translate(0, 20%) rotate(-18deg); }
			@include mq($max-width: 575px) {
				width: 80px;
				height: 80px;
				right: 10px; }
			&__text {
				width: 100%;
				text-align: center;
				color: white;
				font-size: 18px;
				font-weight: 700;
				@include mq($max-width: 575px) {
					font-size: 14px; }
				span {
					display: block;
					font-size: 28px;
					@include mq($max-width: 575px) {
						font-size: 16px; } } } }
		.tape1 {
			position: absolute;
			right: 0;
			top: 0;
			transform: translate(45%, 0);
			z-index: 2;
			@include mq($max-width: 1200px) {
				display: none; } }
		.tape2 {
			position: absolute;
			left: 0;
			bottom: 0;
			z-index: 2;
			@include mq($max-width: 1200px) {
				display: none; } }
		.cat {
			position: absolute;
			right: 0;
			bottom: 0;
			z-index: 3;
			transform: translate(10%, -5%);
			@include mq($max-width: 1500px) {
				height: 80%; }
			@include mq($max-width: 1200px) {
				transform: translate(10%, 0); }
			@include mq($max-width: 700px) {
				height: 60%; }
			@include mq($max-width: 575px) {
				transform: translate(40%, 0); } }
		.logo {
			position: absolute;
			left: 30%;
			top: 5%;
			transform: translate(0, 10%);
			@include mq($max-width: 1500px) {
				width: 25%; }
			@include mq($max-width: 1200px) {
				left: auto;
				right: 2%; }
			@include mq($max-width: 700px) {
				display: none; } }
		.food {
			position: absolute;
			left: 0;
			bottom: 0;
			transform: translate(10%, -10%);
			@include mq($max-width: 1500px) {
				width: 50%; }
			@include mq($max-width: 700px) {
				display: none; } } }
	.arrow {
		z-index: 4;
		@include mq($max-width: 1200px) {
			top: auto;
			bottom: 0; }
		svg {
			path {
				fill: $green; } }
		&:hover {
			svg {
				path {
					fill: $pink; } } }
		&_prev {
			left: 0;
			transform: translate(50%, -50%);
			@include mq($max-width: 1500px) {
				transform: translate(20%, -50%); }
			@include mq($max-width: 1200px) {
				left: 45%; }
			@include mq($max-width: 1000px) {
				left: 42%; }
			@include mq($max-width: 575px) {
				left: 0; } }
		&_next {
			right: 0;
			transform: translate(-50%, -50%);
			@include mq($max-width: 1500px) {
				transform: translate(-20%, -50%); }
			@include mq($max-width: 1200px) {
				right: 45%; }
			@include mq($max-width: 1000px) {
				right: 42%; }
			@include mq($max-width: 575px) {
				right: auto;
				left: 50px; } } } }

