// main page elements
body {
	font-size: 14px;
	color: $dark;
	font-family: 'Gilroy', sans-serif;
	font-weight: 400;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.no-page {
	margin: 150px 0;
	.action {
		max-width: 350px; } }

.block-title {
	font-family: 'Mandy', serif;
	font-size: 35px;
	color: $pink;
	margin: 0 0 36px 0;
	@include mq($max-width: 1200px) {
		margin: 0 0 20px 0; }
	@include mq($max-width: 575px) {
		font-size: 24px; } }

.arrow {
	position: absolute;
	top: 50%;
	z-index: 3;
	cursor: pointer;
	background: transparent;
	@include mq($max-width: 1200px) {
		top: -45px; }
	@include mq($max-width: 700px) {
		top: -35px; }
	&:hover {
		svg {
			path {
				fill: $green; } } }
	svg {
		display: block;
		width: 50px;
		height: auto;
		@include mq($max-width: 1200px) {
			width: 30px; }
		path {
			@include trs;
			fill: $pink; } }
	&_prev {
		transform: translate(-120%, -50%);
		left: 0;
		@include mq($max-width: 1200px) {
			right: 15px;
			left: auto; } }
	&_next {
		transform: translate(120%, -50%);
		right: 0;
		@include mq($max-width: 1200px) {
			right: 0;
			transform: translate(0, -50%); } } }

.pink-btn {
	text-align: center;
	min-width: 213px;
	padding: 21px 52px;
	background: $pink;
	color: white;
	font-weight: 700;
	font-size: 18px;
	line-height: 1;
	cursor: pointer;
	@include trs;
	@include mq($max-width: 1200px) {
		padding: 10px 20px; }
	@include mq($max-width: 575px) {
		min-width: 150px; }
	&:hover {
		background: $green; } }

.button {
	@include trs;
	cursor: pointer;
	width: 50%;
	background: $green;
	padding: 9px;
	font-weight: 700;
	font-size: 18px;
	line-height: 1;
	color: white;
	height: 52px;
	@include flex(row, center, center, wrap);
	max-width: 213px;

	&_green {
		&:hover {
			background: $red; } }

	&_red {
		background: $pink;
		&:hover {
			background: $green; } } }


