.products-slider {
	padding: 70px 0;
	@include mq($max-width: 1500px) {
		padding: 30px 0; }
	.container {
		max-width: 1560px;
		@include mq($max-width: 1800px) {
			max-width: 1270px; }
		@include mq($max-width: 1500px) {
			max-width: 1100px; } }
	.block-title {
		color: $green; }
	.slider {
		margin: 40px 0 0 0;
		@include mq($max-width: 1200px) {
			margin: 20px 0 0 0; } }
	.slide {
		@include mq($max-width: 1800px) {
			margin: 0 10px; } }
	.product {
		margin: 0 auto; }
	.arrow {
		svg {
			path {
				fill: $green; } }
		&:hover {
			svg {
				path {
					fill: $pink; } } } }
	.product {
		@include mq($max-width: 1800px) {
			max-width: 100%; } } }
